/**********************
 * Analyzer Component *
 **********************/
import React, {ReactElement} from 'react';

/** Components. */
import {Grid, Button, withWidth} from '@material-ui/core';
import {withStyles} from '@material-ui/styles';
import {Timer, BlurOn} from '@material-ui/icons';

import Context from '../provider/Context';
import STYLE from './Analyzer.style';

interface AnalyzerProps {
  classes: {
    analyzer: string;
    button: string;
  };
  width: string;
}
class AnalyzerComponent extends React.Component<AnalyzerProps> {
  get score(): string {
    const prefix: string = this.props.width === 'xs' ? '' : 'Score: ';
    return `${prefix}${this.context.aiScore}`;
  }

  get timer(): string {
    const prefix: string = this.props.width === 'xs' ? '' : 'Timer: ';
    return `${prefix}${this.context.aiTimer / 1000}s`;
  }

  render(): ReactElement {
    const {analyzer} = this.props.classes;
    return (
      <Grid
        className={analyzer}
        container
        direction='row'
        justify='space-between'
        spacing={1}>
        <Grid item xs={6}>
          <Button
            color='primary'
            disableRipple
            fullWidth
            startIcon={<BlurOn />}
            variant='outlined'>
            {this.score}
          </Button>
        </Grid>
        <Grid item xs={6}>
          <Button
            color='primary'
            disableRipple
            fullWidth
            startIcon={<Timer />}
            variant='outlined'>
            {this.timer}
          </Button>
        </Grid>
      </Grid>
    );
  }
}

AnalyzerComponent.contextType = Context;
export default withWidth()(withStyles(STYLE)(AnalyzerComponent));
