import {Theme} from '@material-ui/core';

/***************
 * Screen Util *
 ***************/
const SM_SCREEN_1 = 560;
const SM_SCREEN_2 = 450;
export const MOBILE_SCREEN = 380;

export const pageMaxWidth = (theme: Theme) => ({
  maxWidth: theme.spacing(69),
  [theme.breakpoints.down(SM_SCREEN_1)]: {
    maxWidth: theme.spacing(52),
  },
  [theme.breakpoints.down(SM_SCREEN_2)]: {
    maxWidth: theme.spacing(48),
  },
  [theme.breakpoints.down(MOBILE_SCREEN)]: {
    maxWidth: 'inherit',
  },
});
