/** Constants. */
import {SEARCH_DEPTH, SPREAD_LIMIT} from '../constants/ai';

/** Model. */
import Board from './board';
import Timer from './timer';
import SearchCache from './search-cache';

/** Util. */
import {greaterThanOrEqualTo, lessThanOrEqualTo, equalTo} from '../utils/math';
import {nextRole} from '../utils/next';

/** Types. */
import {SearchRecord, SearchStep} from '../types/search';
import {Role} from '../types/role';
import {Step} from '../types/board';
import {Score} from '../types/score';

export default class NegaMax {
  searchCache: SearchCache = new SearchCache();
  timer: Timer = new Timer();
  board: Board = new Board();
  leafCount: number = 0;
  searchRecordCount: number = 0;
  tooDeepCount: number = 0;
  lowScoreCount: number = 0;
  normalCount: number = 0;
  callCount: number = 0;
  alphaCount: number = 0;

  constructor(board: Board) {
    this.board = board;
    this.reset();
  }

  reset() {
    this.searchCache.reset();
    this.timer.reset();
  }

  _getBestSearchRecord = (
    depth: number,
    alpha: number,
    beta: number,
    role: Role,
    stepCount: number,
    steps: Step[],
    spread: number
  ): SearchRecord => {
    this.callCount++;
    const currentSearchPoint = this.searchCache.get(
      this.board.zobrist.hashCode
    );
    if (currentSearchPoint) {
      const {record} = currentSearchPoint;
      if (currentSearchPoint.depth >= depth) {
        this.tooDeepCount++;
        return {
          boardScore: record.boardScore,
          stepCount: stepCount + record.stepCount,
          steps,
        };
      } else {
        if (
          greaterThanOrEqualTo(record.boardScore, Score.Four) ||
          lessThanOrEqualTo(record.boardScore, -Score.Four)
        ) {
          // lowScoreCount++;
          return record;
        }
      }
    }

    /**************
     * TOUCH LEAF *
     **************/
    const boardScore = this.board.evaluateBoard(role);
    const leaf = {boardScore, stepCount, steps};
    if (
      depth <= 0 ||
      greaterThanOrEqualTo(boardScore, Score.Five) ||
      lessThanOrEqualTo(boardScore, -Score.Five)
    ) {
      // leafCount++;
      return leaf;
    }

    const isOnlyThrees =
      this.board.stepCount > 10 ? stepCount > 1 : stepCount > 3;
    const applyStarSpread = stepCount > 1; // Turn on starSpread after init.
    let heuristicSteps = this.board.heuristicEvaluate(
      role,
      isOnlyThrees,
      applyStarSpread
    );
    if (!heuristicSteps.length) {
      // leafCount++;
      return leaf;
    }

    /**********************
     * BEST SEARCH RECORD *
     **********************/
    let bestSearchRecord = {boardScore: Score.Min, stepCount, steps};
    for (const heuristicStep of heuristicSteps) {
      this.alphaCount++;
      this.board.placeChess(heuristicStep, role);
      let [nextDepth, nextSpread] = [depth - 1, spread];
      if (nextSpread < SPREAD_LIMIT) {
        if (
          (role === Role.Agent && heuristicStep.humanScore >= Score.Five) ||
          (role === Role.Human && heuristicStep.agentScore >= Score.Five)
        ) {
          nextDepth += 2;
          nextSpread++;
        }
      }

      const nextSteps = steps.slice();
      nextSteps.push(heuristicStep);

      const searchRecord = this._getBestSearchRecord(
        nextDepth,
        -beta,
        -alpha,
        nextRole(role),
        stepCount + 1,
        nextSteps,
        nextSpread
      );

      searchRecord.boardScore *= -1;
      this.board.cleanChess(heuristicStep);

      if (searchRecord.boardScore > bestSearchRecord.boardScore) {
        bestSearchRecord = searchRecord;
      }
      alpha = Math.max(bestSearchRecord.boardScore, alpha);
      if (greaterThanOrEqualTo(searchRecord.boardScore, beta)) {
        searchRecord.boardScore = Score.Max - 1;
        searchRecord.alphaBetaPrune = 1;
        this.searchRecordCount++;
        return searchRecord;
      }
    }
    this.normalCount++;
    this.searchCache.set(this.board.zobrist.hashCode, bestSearchRecord, depth);
    return bestSearchRecord;
  };

  _getAlpha = (
    heuristicSteps: Step[],
    role: Role,
    depth: number,
    alpha: number,
    beta: number
  ): number => {
    this.board.optionalSteps = [];
    for (const heuristicStep of heuristicSteps) {
      this.board.placeChess(heuristicStep, role);
      const steps = [heuristicStep];
      const bestSearchRecord = this._getBestSearchRecord(
        depth - 1,
        -beta,
        -alpha,
        nextRole(role),
        1,
        steps.slice(),
        0
      );

      bestSearchRecord.boardScore *= -1;
      alpha = Math.max(alpha, bestSearchRecord.boardScore);

      this.board.cleanChess(heuristicStep);
      heuristicStep.record = bestSearchRecord;

      if (this.timer.expired) {
        console.log('timeout...');
        break;
      }
    }
    return alpha;
  };

  _depthSearch = (
    heuristicSteps: Step[],
    role: Role,
    searchDepth: number
  ): SearchStep => {
    this.timer.reset();
    this.searchCache.reset();
    // const start = (+ new Date());
    for (let depth = 2; depth <= searchDepth; depth += 2) {
      // Remove a LOSS point per traverse.
      // This call will update the heuristicSteps.
      const bestScore = this._getAlpha(
        heuristicSteps,
        role,
        depth,
        Score.Min,
        Score.Max
      );
      // Win point!
      if (greaterThanOrEqualTo(bestScore, Score.Five)) {
        break;
      }
    }

    const heuristicSearchRecords: SearchStep[] = heuristicSteps.map(
      (heuristicStep) => ({
        point: heuristicStep.point,
        boardScore: heuristicStep.record!.boardScore,
        stepCount: heuristicStep.record!.stepCount,
        steps: heuristicStep.record!.steps,
        timer: this.timer.consumed,
      })
    );

    heuristicSearchRecords.sort((heuristicStepA, heuristicStepB) => {
      if (equalTo(heuristicStepA.boardScore, heuristicStepB.boardScore)) {
        if (heuristicStepA.boardScore >= 0) {
          // In advantage! Try cut the steps.
          if (heuristicStepA.stepCount !== heuristicStepB.stepCount) {
            return heuristicStepA.stepCount - heuristicStepB.stepCount;
          } else {
            return heuristicStepB.boardScore - heuristicStepA.boardScore;
          }
        } else {
          // In disadvantage! Try extend the steps.
          if (heuristicStepA.stepCount !== heuristicStepB.stepCount) {
            return heuristicStepB.stepCount - heuristicStepA.stepCount;
          } else {
            return heuristicStepB.boardScore - heuristicStepA.boardScore;
          }
        }
      } else {
        return heuristicStepB.boardScore - heuristicStepA.boardScore;
      }
    });
    // console.log(`placeCount: ${this.board.placeCount}`);
    // console.log(`leafCount: ${this.leafCount}`);
    // console.log(`searchRecordCount: ${this.searchRecordCount}`);
    // console.log(`tooDeepCount: ${this.tooDeepCount}`);
    // console.log(`lowScoreCount: ${this.lowScoreCount}`);
    // console.log(`normalCount: ${this.normalCount}`);
    // console.log(`callCount: ${this.callCount}`);
    // console.log(`alphaCount: ${this.alphaCount}`);
    return heuristicSearchRecords[0];
  };

  getSearchStep = (
    currentRole: Role | undefined,
    searchDepth: number | undefined
  ): SearchStep => {
    currentRole = currentRole || Role.Agent;
    searchDepth = searchDepth || SEARCH_DEPTH;
    const heuristicSteps = this.board.heuristicEvaluate(
      currentRole,
      false,
      false
    );
    return this._depthSearch(heuristicSteps, currentRole, searchDepth);
  };
}
