import {THRESHOLD} from '../constants/game';

/** Types. */
import {Step} from '../types/board';
import {Score} from '../types/score';

/**
 * Compares two numbers
 * @param {number} a, the first number to compare.
 * @param {number} b, the second number to compare.
 * @returns {boolean} returns true if they are within the threshold limit.
 */
export const equalTo = (a: number, b: number = 0.01): boolean =>
  b >= 0
    ? a >= b / THRESHOLD && a <= b * THRESHOLD
    : a >= b * THRESHOLD && a <= b / THRESHOLD;

/**
 * Compares two numbers
 * @param {number} a, the first number to compare.
 * @param {number} b, the second number to compare.
 * @returns {boolean} returns true if the first number is greater than the
 * second number times threshold limit.
 */
const greaterThan = (a: number, b: number): boolean =>
  b >= 0 ? a >= (b + 0.1) * THRESHOLD : a >= (b + 0.1) / THRESHOLD;

/**
 * Compares two numbers
 * @param {number} a, the first number to compare.
 * @param {number} b, the second number to compare.
 * @returns {boolean} returns true if the first number is less than the
 * second number divides by the threshold limit.
 */
const lessThan = (a: number, b: number): boolean =>
  b >= 0 ? a <= (b - 0.1) / THRESHOLD : a <= (b - 0.1) * THRESHOLD;

/**
 * Compares two numbers
 * @param {number} a, the first number to compare.
 * @param {number} b, the second number to compare.
 * @returns {boolean} returns true if the first number is greater than or equal
 * to the second number times threshold limit.
 */
export const greaterThanOrEqualTo = (a: number, b: number): boolean =>
  equalTo(a, b) || greaterThan(a, b);

/**
 * Compares two numbers
 * @param {number} a, the first number to compare.
 * @param {number} b, the second number to compare.
 * @returns {boolean} returns true if the first number is less than or equal to
 * the second number divides by the threshold limit.
 */
export const lessThanOrEqualTo = (a: number, b: number): boolean =>
  equalTo(a, b) || lessThan(a, b);

/**
 * Checks if points contains a target point.
 * @param {Step[]} steps.
 * @param {Step} targetStep.
 * @returns {boolean} returns true if points contains the targetStep.
 */
export const containsStep = (steps: Step[], targetStep: Step): boolean => {
  const [targetRow, targetCol] = targetStep.point;
  for (const step of steps) {
    const [pointRow, pointCol] = step.point;
    if (pointRow === targetRow && pointCol === targetCol) {
      return true;
    }
  }
  return false;
};

/**
 * Checks if two points has the same coordinates.
 * @param {Array<number, number>} pointA, the first point to check.
 * @param {Array<number, number>} pointB, the second point to check.
 * @returns {boolean} returns true if two points has the same coordinates.
 */
export const pointEqual = (
  [aRow, aCol]: number[],
  [bRow, bCol]: number[]
): boolean => aRow === bRow && aCol === bCol;

/** Private helper method. */
const scoreBetween = (
  score: number,
  lowerBound: number,
  upperBound: number
): boolean => {
  return score > lowerBound && score <= upperBound;
};

/**
 * Rounds the score.
 * @param {number} score, the score to convert.
 * @returns {number} returns the converted score.
 */
export const round = (score: number): number => {
  const sign = score < 0 ? -1 : 1;
  const absoluteScore = Math.abs(score);
  if (absoluteScore <= Score.One / 2) {
    return 0;
  }
  if (scoreBetween(absoluteScore, Score.One / 2, Score.Two / 2)) {
    return sign * Score.One;
  }
  if (scoreBetween(absoluteScore, Score.Two / 2, Score.Three / 2)) {
    return sign * Score.Two;
  }
  if (scoreBetween(absoluteScore, Score.Three / 2, Score.Three * 1.5)) {
    return sign * Score.Three;
  }
  if (scoreBetween(absoluteScore, Score.Three * 1.5, Score.Four / 2)) {
    return sign * Score.Three * 2;
  }
  if (scoreBetween(absoluteScore, Score.Four / 2, Score.Five / 2)) {
    return sign * Score.Four;
  }
  return sign * Score.Five;
};
