import {MersenneTwister19937, integer} from 'random-js';

/** Constants. */
import {BOARD_SIZE} from '../constants/game';

/** Util. */
import {buildBoard} from '../utils/generator';
import {Role} from '../types/role';

const engine = MersenneTwister19937.autoSeed();

export default class Zobrist {
  hashCode: number = this._random();
  agentStepTags: Role[][] = buildBoard();
  humanStepTags: Role[][] = buildBoard();

  constructor() {
    this.reset();
  }

  _random(): number {
    return integer(1, 1000000000)(engine);
  }

  reset(): void {
    for (let i = 0; i < BOARD_SIZE; i++) {
      for (let j = 0; j < BOARD_SIZE; j++) {
        this.agentStepTags[i][j] = this._random();
        this.humanStepTags[i][j] = this._random();
      }
    }
  }

  updateHashCode(
    currentRow: number,
    currentCol: number,
    currentRole: number
  ): void {
    this.hashCode ^=
      currentRole === Role.Agent
        ? this.agentStepTags[currentRow][currentCol]
        : this.humanStepTags[currentRow][currentCol];
  }
}
