/** Constant. */
import {BOARD_SIZE} from '../constants/game';

/** Types. */
import {Winner} from '../types/board';
import {Role} from '../types/role';
import {MoveDirection} from '../types/direction';
import Board from '../models/board';

/**
 * It will be guarantee that the role passed in will not be EMPTY.
 * Returns array of points with consecutive roles.
 */
export const getFiveConsecutivePoints = (
  board: Role[][],
  currentRow: number,
  currentCol: number,
  currentRole: Role
): number[][] => {
  let consecutivePoints: number[][] = [];
  const resetConsecutivePoints = (): void => {
    consecutivePoints = [];
  };
  const withinBound = (row: number, col: number): boolean =>
    row >= 0 && col >= 0 && row < BOARD_SIZE && col < BOARD_SIZE;

  const {Row, Col, Des, Asc} = MoveDirection;
  const moveDirections = [Row, Col, Des, Asc];
  for (const moveDirection of moveDirections) {
    resetConsecutivePoints();
    for (let r = 0; r < 5; r++) {
      let [row, col] = [currentRow, currentCol + r];
      switch (moveDirection) {
        case Row:
          [row, col] = [currentRow, currentCol + r];
          break;
        case Col:
          [row, col] = [currentRow + r, currentCol];
          break;
        case Des:
          [row, col] = [currentRow + r, currentCol + r];
          break;
        case Asc:
          [row, col] = [currentRow + r, currentCol - r];
          break;
        default: // do not do anything.
      }
      if (!withinBound(row, col) || board[row][col] !== currentRole) {
        break;
      }
      consecutivePoints.push([row, col]);
    }
    if (consecutivePoints.length === 5) {
      return consecutivePoints;
    }
  }
  return [];
};

/**
 * Returns empty object if there is no winner.
 * Otherwise, return an object with role and winPoints.
 */
export const getWinner = (gameBoard: Board): Winner => {
  const {board} = gameBoard;
  for (let i = 0; i < BOARD_SIZE; i++) {
    for (let j = 0; j < BOARD_SIZE; j++) {
      const role = board[i][j];
      if (role !== Role.Empty) {
        const winPoints = getFiveConsecutivePoints(board, i, j, role);
        if (winPoints.length) {
          return {role, winPoints};
        }
      }
    }
  }
  return {role: Role.Empty, winPoints: []};
};

/** Counts the steps on board. */
export const getStepCount = (board: Role[][]): number => {
  let count = 0;
  for (const row of board) {
    for (const role of row) {
      if (role !== Role.Empty) {
        count++;
      }
    }
  }
  return count;
};
