import {yellow, teal} from '@material-ui/core/colors';

/** Constants. */
import {COUNT_LIMIT} from '../constants/ai';
import {BOARD_SIZE} from '../constants/game';

/** Types. */
import {DisplayStep, Step} from '../types/board';
import {ScoreCache} from '../types/cache';
import {MoveDirection} from '../types/direction';
import {Role} from '../types/role';
import {Score} from '../types/score';

/** Utils. */
import {getStepCount} from '../utils/control';
import {evaluatePoint, reinforceScore} from '../utils/evaluate/point';
import {
  buildBoard,
  buildDisplayBoard,
  buildScoreCache,
} from '../utils/generator';
import {nextRole} from '../utils/next';
import {shouldConsiderStep} from '../utils/validator';

/** Model. */
import Zobrist from './zobrist';
import {GameMode} from '../types/game';

export default class Board {
  zobrist: Zobrist = new Zobrist();
  board: Role[][] = [];
  displayBoard: DisplayStep[][] = [];
  scoreCache: ScoreCache = [];
  optionalSteps: Step[] = [];
  recordedSteps: Step[] = [];
  backwardSteps: Step[] = [];
  agentScores: number[][] = [];
  humanScores: number[][] = [];
  stepCount: number = 0;
  placeCount: number = 0;
  cleanCount: number = 0;

  constructor() {
    this.reset();
  }

  reset() {
    this.zobrist.reset();
    this.board = buildBoard();
    this.displayBoard = buildDisplayBoard();
    this.scoreCache = buildScoreCache();
    this.optionalSteps = [];
    this.recordedSteps = [];
    this.backwardSteps = [];
    this.agentScores = buildBoard();
    this.humanScores = buildBoard();
    this.stepCount = 0;
    this.placeCount = 0;
    this.cleanCount = 0;
  }

  /** Initialize the board with an open board. */
  initWithBoard(openBoard: Role[][]): void {
    this.board = openBoard;
    this.stepCount = getStepCount(openBoard);
    this._initScore();
  }

  /**  Score for AGENT and HUMAN. */
  _initScore() {
    const {board, scoreCache} = this;
    for (let i = 0; i < BOARD_SIZE; i++) {
      for (let j = 0; j < BOARD_SIZE; j++) {
        const currentRole = this.board[i][j];
        if (currentRole === Role.Empty) {
          if (this._hasNeighbor(i, j, 2, 2)) {
            // Only score if the EMPTY point has neighbor.
            this.agentScores[i][j] = evaluatePoint(
              board,
              scoreCache,
              i,
              j,
              Role.Agent,
              undefined
            );
            this.humanScores[i][j] = evaluatePoint(
              board,
              scoreCache,
              i,
              j,
              Role.Human
            );
          }
        } else if (currentRole === Role.Agent) {
          // Score the agent; the human should be 0.
          this.agentScores[i][j] = evaluatePoint(
            board,
            scoreCache,
            i,
            j,
            Role.Agent
          );
          this.humanScores[i][j] = 0;
        } else if (currentRole === Role.Human) {
          // Score the human; the agent should be 0.
          this.humanScores[i][j] = evaluatePoint(
            board,
            scoreCache,
            i,
            j,
            Role.Human
          );
          this.agentScores[i][j] = 0;
        }
      }
    }
  }

  /** Updates the score for each point. */
  _updateRoleBoardScore = (
    row: number,
    col: number,
    moveDirection: MoveDirection
  ): void => {
    const role: Role = this.board[row][col];
    const {board, scoreCache} = this;
    if (role !== Role.Human) {
      this.agentScores[row][col] = evaluatePoint(
        board,
        scoreCache,
        row,
        col,
        Role.Agent,
        moveDirection
      );
    } else {
      this.agentScores[row][col] = 0;
    }
    if (role !== Role.Agent) {
      this.humanScores[row][col] = evaluatePoint(
        board,
        scoreCache,
        row,
        col,
        Role.Human,
        moveDirection
      );
    } else {
      this.humanScores[row][col] = 0;
    }
  };

  /** Updates a score on nearby points. */
  _updateScoreOnMoveDirection(currentRow: number, currentCol: number): void {
    const radius = 4;
    const {Row, Col, Des, Asc} = MoveDirection;
    // case: -
    for (let i = -radius; i <= radius; i++) {
      const [row, col] = [currentRow, currentCol + i];
      if (col < 0) continue;
      if (col >= BOARD_SIZE) break;
      this._updateRoleBoardScore(row, col, Row);
    }

    // case: |
    for (let i = -radius; i <= radius; i++) {
      const [row, col] = [currentRow + i, currentCol];
      if (row < 0) continue;
      if (row >= BOARD_SIZE) break;
      this._updateRoleBoardScore(row, col, Col);
    }

    // case: \
    for (let i = -radius; i <= radius; i++) {
      const [row, col] = [currentRow + i, currentCol + i];
      if (row < 0 || col < 0) continue;
      if (row >= BOARD_SIZE || col >= BOARD_SIZE) break;
      this._updateRoleBoardScore(row, col, Des);
    }

    // case: /
    for (let i = -radius; i <= radius; i++) {
      const [row, col] = [currentRow + i, currentCol - i];
      if (row < 0 || col < 0) continue;
      if (row >= BOARD_SIZE || col >= BOARD_SIZE) continue;
      this._updateRoleBoardScore(row, col, Asc);
    }
  }

  updateWinPoints(points: number[][]) {
    for (const [row, col] of points) {
      const displayStep: DisplayStep = this.displayBoard[row][col];
      displayStep.backgroundColor = yellow[600];
      displayStep.color = teal[600];
    }
  }

  /** Updates on a single cell. */
  _updateDisplayBoard(row: number, col: number, placeChess: boolean): void {
    if (placeChess) {
      // Mark it black if the stepCount is in odd.
      const isBlack = this.stepCount & 1;
      this.displayBoard[row][col] = {
        color: isBlack ? 'white' : 'black',
        backgroundColor: isBlack ? 'black' : 'white',
        shadow: 2,
        label: this.stepCount,
      };
    } else {
      this.displayBoard[row][col] = {
        color: 'inherit',
        backgroundColor: 'inherit',
        shadow: 0,
        label: 0,
      };
    }
  }

  /** Drop the chess. */
  placeChess(heuristicStep: Step, role: Role): void {
    this.placeCount++;

    const [row, col] = heuristicStep.point;
    heuristicStep.role = role;
    this.board[row][col] = role;
    this.stepCount++;
    this._updateDisplayBoard(row, col, true);

    this.zobrist.updateHashCode(row, col, role);
    this._updateScoreOnMoveDirection(row, col);
    this.recordedSteps.push(heuristicStep);
    this.optionalSteps.push(heuristicStep);
  }

  /** Remove the chess. */
  cleanChess(heuristicStep: Step) {
    this.cleanCount++;

    const [row, col] = heuristicStep.point;
    const role = this.board[row][col];
    this.board[row][col] = Role.Empty;
    this.stepCount--;
    this._updateDisplayBoard(row, col, false);

    this.zobrist.updateHashCode(row, col, role);
    this._updateScoreOnMoveDirection(row, col);
    this.recordedSteps.pop();
    this.optionalSteps.pop();
  }

  stepBackward(mode: GameMode) {
    const countStep = mode === GameMode.VsAgent ? 2 : 1;
    if (this.recordedSteps.length < countStep) {
      return;
    }
    for (let i = 0; i < countStep; i++) {
      const step = this.recordedSteps[this.recordedSteps.length - 1];
      this.cleanChess(step);
      this.backwardSteps.push(step);
    }
  }

  stepForward(mode: GameMode) {
    const countStep = mode === GameMode.VsAgent ? 2 : 1;
    if (this.backwardSteps.length < countStep) {
      return;
    }
    for (let i = 0; i < countStep; i++) {
      const step: Step = this.backwardSteps.pop()!;
      this.placeChess(step, step.role);
    }
  }

  _hasNeighbor(
    currentRow: number,
    currentCol: number,
    radius: number,
    emptyCount: number
  ): boolean {
    const {board} = this;
    const [rowA, rowB] = [currentRow - radius, currentRow + radius];
    const [colA, colB] = [currentCol - radius, currentCol + radius];

    for (let i = rowA; i <= rowB; i++) {
      if (i < 0 || i >= BOARD_SIZE) {
        continue;
      }
      for (let j = colA; j <= colB; j++) {
        if (j < 0 || j >= BOARD_SIZE) {
          continue;
        }
        if (i === currentRow && j === currentCol) {
          continue;
        }
        if (board[i][j] !== Role.Empty) {
          emptyCount--;
          if (emptyCount <= 0) {
            return true;
          }
        }
      }
    }
    return false;
  }

  _updateStepsOnSkipLayers(steps: Step[], role: Role, i: number): void {
    while (i >= 0) {
      const step = this.optionalSteps[i];
      if (
        (role === Role.Agent && step.agentScore >= Score.Three) ||
        (role === Role.Human && step.humanScore >= Score.Three)
      ) {
        steps.push(step);
        break;
      }
      i -= 2;
    }
    if (!steps.length) {
      steps.push(
        this.optionalSteps[0].role === role
          ? this.optionalSteps[0]
          : this.optionalSteps[1]
      );
    }
  }

  /** Evaluates the score to the entire board. (Before make a step) */
  evaluateBoard(currentRole: Role) {
    let [agentMaxScore, humanMaxScore] = [0, 0];
    const {board} = this;
    // Traverse and find the highest score.
    for (let i = 0; i < BOARD_SIZE; i++) {
      for (let j = 0; j < BOARD_SIZE; j++) {
        if (board[i][j] === Role.Agent) {
          agentMaxScore += reinforceScore(this.agentScores[i][j]);
        }
        if (board[i][j] === Role.Human) {
          humanMaxScore += reinforceScore(this.humanScores[i][j]);
        }
      }
    }
    const sign: number = currentRole === Role.Agent ? 1 : -1;
    const delta: number = agentMaxScore - humanMaxScore;
    return sign * delta;
  }

  /** Heuristic evaluation function. */
  heuristicEvaluate(
    currentRole: Role,
    isOnlyThrees: boolean,
    applyStarSpread: boolean
  ): Step[] {
    // returns a single step.
    if (this.stepCount <= 0) {
      const step: Step = {
        point: [Math.floor(BOARD_SIZE / 2), Math.floor(BOARD_SIZE / 2)],
        humanScore: 0,
        agentScore: 0,
        score: 0,
        role: currentRole,
      };
      return [step];
    }
    const fives: Step[] = [];
    const neighbors: Step[] = [];
    const attackSteps: Step[] = [];
    const defendSteps: Step[] = [];
    const agentFours: Step[] = [];
    const agentBlockedFours: Step[] = [];
    const agentDoubleThrees: Step[] = [];
    const agentThrees: Step[] = [];
    const agentTwos: Step[] = [];
    const humanFours: Step[] = [];
    const humanBlockedFours: Step[] = [];
    const humanDoubleThrees: Step[] = [];
    const humanThrees: Step[] = [];
    const humanTwos: Step[] = [];

    const {board} = this;
    if (applyStarSpread) {
      this._updateStepsOnSkipLayers(
        defendSteps,
        nextRole(currentRole),
        this.optionalSteps.length - 1
      );
      this._updateStepsOnSkipLayers(
        attackSteps,
        currentRole,
        this.optionalSteps.length - 2
      );
    }

    for (let i = 0; i < BOARD_SIZE; i++) {
      for (let j = 0; j < BOARD_SIZE; j++) {
        if (board[i][j] === Role.Empty) {
          if (this.recordedSteps.length < 6) {
            if (!this._hasNeighbor(i, j, 1, 1)) {
              continue;
            }
          } else if (!this._hasNeighbor(i, j, 2, 2)) {
            continue;
          }

          const agentScore = this.agentScores[i][j];
          const humanScore = this.humanScores[i][j];
          const maxScore = Math.max(agentScore, humanScore);
          if (isOnlyThrees && maxScore < Score.Three) {
            continue;
          }

          const step: Step = {
            point: [i, j],
            humanScore: humanScore,
            agentScore: agentScore,
            score: maxScore,
            role: currentRole,
          };

          if (applyStarSpread) {
            const lastStep = this.optionalSteps[this.optionalSteps.length - 1];
            if (maxScore >= Score.Four) {
              // handle here
            } else if (
              maxScore >= Score.BlockedFour &&
              shouldConsiderStep(lastStep, defendSteps)
            ) {
              //star 
            } else if (
              shouldConsiderStep(step, attackSteps) ||
              shouldConsiderStep(step, defendSteps)
            ) {
              // handle case
            } else {
              continue;
            }
          }

          if (agentScore >= Score.Five) {
            fives.push(step);
          } else if (humanScore >= Score.Five) {
            fives.push(step);
          } else if (agentScore >= Score.Four) {
            agentFours.push(step);
          } else if (humanScore >= Score.Four) {
            humanFours.push(step);
          } else if (agentScore >= Score.BlockedFour) {
            agentBlockedFours.push(step);
          } else if (humanScore >= Score.BlockedFour) {
            humanBlockedFours.push(step);
          } else if (agentScore >= 2 * Score.Three) {
            agentDoubleThrees.push(step);
          } else if (humanScore >= 2 * Score.Three) {
            humanDoubleThrees.push(step);
          } else if (agentScore >= Score.Three) {
            agentThrees.push(step);
          } else if (humanScore >= Score.Three) {
            humanThrees.push(step);
          } else if (agentScore >= Score.Two) {
            agentTwos.unshift(step);
          } else if (humanScore >= Score.Two) {
            humanTwos.unshift(step);
          } else {
            neighbors.push(step);
          }
        }
      }
    }

    /*************
     * CASE FIVE *
     *************/
    // Killing step
    if (fives.length) {
      return fives;
    }

    /*************
     * CASE FOUR *
     *************/
    if (currentRole === Role.Agent && agentFours.length) {
      return agentFours;
    }
    if (currentRole === Role.Human && humanFours.length) {
      return humanFours;
    }

    // Opponent has FOUR. Self does not even have BLOCKED_FOUR.
    if (
      currentRole === Role.Agent &&
      humanFours.length &&
      !agentBlockedFours.length
    ) {
      return humanFours;
    }
    if (
      currentRole === Role.Human &&
      agentFours.length &&
      !humanBlockedFours.length
    ) {
      return agentFours;
    }

    /*********************
     * CASE BLOCKED_FOUR *
     *********************/
    // Checks if opponent has FOUR and self has BLOCKED_FOUR.
    const fours: Step[] =
      currentRole === Role.Agent
        ? agentFours.concat(humanFours)
        : humanFours.concat(agentFours);
    const blockedFours =
      currentRole === Role.Agent
        ? agentBlockedFours.concat(humanBlockedFours)
        : humanBlockedFours.concat(agentBlockedFours);
    if (fours.length) {
      return fours.concat(blockedFours);
    }

    /*********************
     * CASE DOUBLE_THREE *
     *********************/
    let result: Step[] = [];
    if (currentRole === Role.Agent) {
      result = agentDoubleThrees
        .concat(humanDoubleThrees)
        .concat(agentBlockedFours)
        .concat(humanBlockedFours)
        .concat(agentThrees)
        .concat(humanThrees);
    }
    if (currentRole === Role.Human) {
      result = humanDoubleThrees
        .concat(agentDoubleThrees)
        .concat(humanBlockedFours)
        .concat(agentBlockedFours)
        .concat(humanThrees)
        .concat(agentThrees);
    }

    if (agentDoubleThrees.length || humanDoubleThrees.length) {
      return result;
    }

    /**************
     * CASE THREE *
     **************/
    if (isOnlyThrees) {
      return result;
    }

    /************
     * CASE TWO *
     ************/
    const twos: Step[] =
      currentRole === Role.Agent
        ? agentTwos.concat(humanTwos)
        : humanTwos.concat(agentTwos);
    twos.sort((a, b) => b.score - a.score);
    result = result.concat(twos.length ? twos : neighbors);
    if (result.length > COUNT_LIMIT) {
      return result.slice(0, COUNT_LIMIT);
    }
    return result;
  }
}
