/******************
 * Cell Component *
 ******************/
import React from 'react';

/** Components. */
import {Grid} from '@material-ui/core';

/** Styles. */
import {withStyles, withTheme} from '@material-ui/styles';

import Context from '../../provider/Context';
import STYLE from './Cell.style';
import {GameStatus} from '../../../types/game';
import {Role} from '../../../types/role';

interface CellProps {
  theme: any;
  classes: {
    cell: string;
  };
  row: number;
  col: number;
  color: string;
  backgroundColor: string;
  label: number;
  shadow: number;
}

class Cell extends React.Component<CellProps> {
  _placeChess(row: number, col: number): void {
    if (
      this.context.board.board[row][col] === Role.Empty &&
      this.context.status === GameStatus.Open &&
      !this.context.aiThinking
    ) {
      this.context.placeChess(row, col);
    }
    if (this.context.status === GameStatus.Over) {
      this.context.openDialog();
    }
  }

  render() {
    const {
      classes,
      theme,
      row,
      col,
      color,
      backgroundColor,
      label,
      shadow,
    } = this.props;

    // Styles
    const boxShadow = theme.shadows[shadow];
    return (
      <Grid
        container
        justify='center'
        alignItems='center'
        className={classes.cell}
        style={{backgroundColor, color, boxShadow}}
        onClick={() => this._placeChess(row, col)}>
        {label ? label : ''}
      </Grid>
    );
  }
}

Cell.contextType = Context;
export default withStyles(STYLE)(withTheme(Cell));
