/********************
 * Header Component *
 ********************/
import React, {ReactElement} from 'react';

/** Components. */
import {
  Button,
  Grid,
  Typography,
  withWidth,
  IconButton,
} from '@material-ui/core';
import {blue, teal, yellow} from '@material-ui/core/colors';
/** Styles. */
import {withStyles} from '@material-ui/core/styles';
import {
  NavigateBefore,
  NavigateNext,
  FilterTiltShift,
} from '@material-ui/icons';

/** Types. */
import {GameState, GameStatus, GameMode} from '../../types/game';
import {Role} from '../../types/role';
/** Components. */
import Context from '../provider/Context';
import {VerticalGap} from '../share/Gap';
import Dialog from './dialog/Dialog';
import STYLE from './Header.style';

interface HeaderProps {
  classes: {
    button: string;
    chess: string;
    header: string;
    message: string;
    notice: string;
    title: string;
  };
  width: string;
}

class Header extends React.Component<HeaderProps> {
  get nextTurnColorNotice(): string {
    const {status, board}: GameState = this.context;
    if (status === GameStatus.Over) {
      return yellow[600];
    }
    return board.stepCount & 1 ? 'white' : 'black';
  }

  get nextStepCountNotice(): number {
    const {board, status, winner}: GameState = this.context;
    if (status === GameStatus.Over) {
      if (winner.role === Role.Empty) {
        return 0;
      } else {
        return board.stepCount;
      }
    } else {
      return board.stepCount + 1;
    }
  }

  get twoPlayersWinColor(): string {
    const {board}: GameState = this.context;
    return board.stepCount & 1 ? 'Black' : 'White';
  }

  get textMessage(): string {
    const {
      aiThinking,
      board,
      mode,
      role,
      status,
      winner,
    }: GameState = this.context;
    if (aiThinking) {
      return `AI is thinking...`;
    }
    if (winner.role !== Role.Empty) {
      if (mode === GameMode.VsAgent) {
        if (winner.role === Role.Human) {
          return `Congratulations! You win!!`;
        } else {
          return `Good luck next time! AI win!!`;
        }
      }
      return `${this.twoPlayersWinColor} role's win!`;
    } else {
      if (status === GameStatus.Over) {
        return `Please start a new game!`;
      } else {
        if (mode === GameMode.VsAgent) {
          if (role === Role.Human) {
            return `Your turn!`;
          } else {
            return `AI turn!`;
          }
        } else {
          if (board.stepCount & 1) {
            return `White role's turn!`;
          } else {
            return `Black role's turn!`;
          }
        }
      }
    }
  }

  get Top(): ReactElement {
    const {classes, width} = this.props;
    const {title, notice, chess} = classes;
    const isXs = width === 'xs';
    return (
      <Grid item xs={12}>
        <Grid
          container
          direction='row'
          justify='space-between'
          alignItems='center'>
          <Grid item xs={6}>
            <Typography variant={isXs ? 'h4' : 'h3'} className={title}>
              GoMoKu
            </Typography>
          </Grid>
          <Grid item xs={4}>
            <Grid container spacing={1} direction='row'>
              <Grid item xs={6}>
                <Grid
                  container
                  direction='column'
                  className={notice}
                  alignItems='center'>
                  <Typography variant={isXs ? 'button' : 'h6'}>Turn</Typography>
                  <VerticalGap size='xs' />
                  <Grid
                    className={chess}
                    style={{backgroundColor: this.nextTurnColorNotice}}
                  />
                </Grid>
              </Grid>
              <Grid item xs={6}>
                <Grid
                  container
                  direction='column'
                  className={notice}
                  alignItems='center'
                  justify='center'>
                  <Typography variant={isXs ? 'button' : 'h6'}>Step</Typography>
                  <Typography
                    variant='body2'
                    align='center'
                    style={{
                      color: this.nextTurnColorNotice,
                      fontWeight: 500,
                    }}>
                    {this.nextStepCountNotice}
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    );
  }

  get Instruction(): ReactElement {
    const {classes, width} = this.props;
    const isXs = width === 'xs';
    const {button, message} = classes;
    return (
      <Grid
        alignItems='center'
        container
        direction='row'
        justify='space-between'>
        <Grid item xs={6}>
          <Typography variant='button' className={message}>
            {this.textMessage}
          </Typography>
        </Grid>
        {this.context.status === GameStatus.Open && (
          <Grid item xs={2}>
            <Grid container direction='row'>
              <Grid item xs={6}>
                <IconButton
                  size='small'
                  color='primary'
                  onClick={() => this.context.stepBackward()}
                  disabled={!this.context.board.recordedSteps.length}>
                  <NavigateBefore />
                </IconButton>
              </Grid>
              <Grid item xs={6}>
                <IconButton
                  size='small'
                  color='primary'
                  onClick={() => this.context.stepForward()}
                  disabled={!this.context.board.backwardSteps.length}>
                  <NavigateNext />
                </IconButton>
              </Grid>
            </Grid>
          </Grid>
        )}
        <Grid item xs={4}>
          <Button
            className={button}
            color='primary'
            fullWidth
            onClick={() => this.context.openDialog()}
            startIcon={<FilterTiltShift />}
            style={{
              backgroundColor:
                this.context.status === GameStatus.Over ? blue[500] : teal[500],
            }}
            variant='contained'>
            <Typography variant={isXs ? 'button' : 'h6'}>
              {isXs ? 'Start' : 'New Game'}
            </Typography>
          </Button>
          <Dialog />
        </Grid>
      </Grid>
    );
  }

  render(): ReactElement {
    const {classes} = this.props;
    return (
      <Grid className={classes.header}>
        {this.Top}
        <VerticalGap size='sm' />
        {this.Instruction}
      </Grid>
    );
  }
}

Header.contextType = Context;

export default withWidth()(withStyles(STYLE)(Header));
