/********************
 * Header Component *
 ********************/
import React, {ReactElement} from 'react';

/** Components. */
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControl,
  FormControlLabel,
  FormLabel,
  Grid,
  Radio,
  RadioGroup,
  Typography,
  withWidth,
} from '@material-ui/core';
import {teal} from '@material-ui/core/colors';

/** Styles. */
import {createStyles, Theme, withStyles} from '@material-ui/core/styles';

/** Types */
import {AiLevel, GameMode} from '../../../types/game';
import {Role} from '../../../types/role';

/** Components. */
import Context from '../../provider/Context';
import {VerticalGap} from '../../share/Gap';

/** Constants. */
import STYLE from './Dialog.style';

interface DialogProps {
  classes: {
    button: string;
    label: string;
  };
  width: string;
}

interface DialogState {
  aiLevel: AiLevel;
  firstHand: Role;
  gameMode: GameMode;
}

const GameDialog = withStyles((theme: Theme) =>
  createStyles({
    paperFullWidth: {
      width: 360,
      [theme.breakpoints.down('xs')]: {
        margin: 0,
        width: 300,
      },
    },
  })
)(Dialog);

class DialogComponent extends React.Component<DialogProps, DialogState> {
  state = {
    aiLevel: AiLevel.easy,
    firstHand: Role.Human,
    gameMode: GameMode.VsAgent,
  };

  setGameMode(gameMode: GameMode): void {
    this.setState({gameMode});
  }

  handleGameModeChange(event: React.ChangeEvent<HTMLInputElement>): void {
    const gameMode: GameMode = Number(event.target.value);
    this.setState({gameMode});
  }

  handleFirstHandChange(event: React.ChangeEvent<HTMLInputElement>): void {
    const firstHand: Role = Number(event.target.value);
    this.setState({firstHand});
  }

  handleAiLevelChange(event: React.ChangeEvent<HTMLInputElement>): void {
    const aiLevel: AiLevel = Number(event.target.value);
    this.setState({aiLevel});
  }

  startNewGame(): void {
    const {aiLevel, firstHand, gameMode}: DialogState = this.state;
    this.context.startNewGame(gameMode, firstHand, aiLevel);
    this.context.closeDialog();
  }

  resetDialog(): void {
    this.setState({
      aiLevel: AiLevel.easy,
      firstHand: Role.Human,
      gameMode: GameMode.VsAgent,
    });
  }

  Label(text: string): ReactElement {
    return <Typography variant='body2'>{text}</Typography>;
  }

  get GameModeOptions(): ReactElement {
    const {gameMode} = this.state;
    const {label} = this.props.classes;
    return (
      <FormControl component='fieldset'>
        <FormLabel component='legend'>
          <Typography variant='button'>Game Mode</Typography>
        </FormLabel>
        <RadioGroup
          value={gameMode}
          onChange={(event) => this.handleGameModeChange(event)}
          row>
          <FormControlLabel
            className={label}
            control={<Radio color='primary' />}
            label={this.Label('2 Players')}
            labelPlacement='end'
            value={GameMode.VsHuman}
          />
          <FormControlLabel
            className={label}
            control={<Radio color='primary' />}
            label={this.Label('VS AI')}
            labelPlacement='end'
            value={GameMode.VsAgent}
          />
        </RadioGroup>
      </FormControl>
    );
  }

  get FirstHandOptions(): ReactElement {
    const {firstHand} = this.state;
    const {label} = this.props.classes;
    return (
      <FormControl component='fieldset'>
        <FormLabel component='legend'>
          <Typography variant='button'>First Hand</Typography>
        </FormLabel>
        <RadioGroup
          value={firstHand}
          onChange={(event) => this.handleFirstHandChange(event)}
          row>
          <FormControlLabel
            className={label}
            control={<Radio color='primary' />}
            label={this.Label('Me First')}
            labelPlacement='end'
            value={Role.Human}
          />
          <FormControlLabel
            className={label}
            control={<Radio color='primary' />}
            label={this.Label('AI First')}
            labelPlacement='end'
            value={Role.Agent}
          />
        </RadioGroup>
      </FormControl>
    );
  }

  get DifficultyOption(): ReactElement {
    const {aiLevel} = this.state;
    const {label} = this.props.classes;
    return (
      <FormControl>
        <FormLabel component='legend'>
          <Typography variant='button'>Level</Typography>
        </FormLabel>
        <RadioGroup
          onChange={(event) => this.handleAiLevelChange(event)}
          value={aiLevel}>
          <FormControlLabel
            className={label}
            control={<Radio color='primary' />}
            label={this.Label('Easy')}
            labelPlacement='end'
            value={AiLevel.easy}
          />
          <FormControlLabel
            className={label}
            control={<Radio color='primary' />}
            label={this.Label('Medium')}
            labelPlacement='end'
            value={AiLevel.medium}
          />
          <FormControlLabel
            className={label}
            control={<Radio color='primary' />}
            label={this.Label('Difficult')}
            labelPlacement='end'
            value={AiLevel.hard}
          />
        </RadioGroup>
      </FormControl>
    );
  }

  render(): ReactElement {
    const {gameMode} = this.state;
    const {button} = this.props.classes;
    return (
      <GameDialog
        fullWidth
        maxWidth='xs'
        onClose={() => this.context.closeDialog()}
        onEnter={() => this.resetDialog()}
        open={this.context.dialogOpen}>
        <DialogTitle style={{color: teal[500]}}>Game Setting</DialogTitle>
        <DialogContent>
          <Grid container direction='column'>
            {this.GameModeOptions}
            <VerticalGap size='md' />
            {gameMode === GameMode.VsAgent && this.FirstHandOptions}
            {gameMode === GameMode.VsAgent && this.DifficultyOption}
          </Grid>
        </DialogContent>
        <VerticalGap size='sm' />
        <DialogActions>
          <Grid item xs={12} sm={6}>
            <Grid container direction='row' spacing={1}>
              <Grid item xs={6}>
                <Button
                  className={button}
                  color='secondary'
                  fullWidth
                  onClick={() => this.context.closeDialog()}
                  size='small'
                  variant='outlined'>
                  Cancel
                </Button>
              </Grid>
              <Grid item xs={6}>
                <Button
                  className={button}
                  color='primary'
                  fullWidth
                  onClick={() => this.startNewGame()}
                  size='small'
                  variant='contained'>
                  Submit
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </DialogActions>
      </GameDialog>
    );
  }
}
DialogComponent.contextType = Context;

export default withWidth()(withStyles(STYLE)(DialogComponent));
