/******************
 * Board Component *
 ******************/
import React, {ReactElement} from 'react';

/** Styles. */
import {withStyles} from '@material-ui/styles';
import STYLE from './Board.style';

/** Components. */
import {Grid} from '@material-ui/core';
import Cell from './cell/Cell';
import Context from '../provider/Context';
import BoardImage from './image/board.png';

/** Model */
import Board from '../../models/board';
import {DisplayStep} from '../../types/board';

interface BoardProps {
  classes: {
    board: string;
    boardWrapper: string;
    boardImage: string;
    chessSteps: string;
  };
}

class BoardComponent extends React.Component<BoardProps> {
  Row(row: DisplayStep[], rowIdx: number): ReactElement[] {
    return row.map(
      (displayStep: DisplayStep, colIdx: number): ReactElement => {
        const {color, backgroundColor, label, shadow} = displayStep;
        return (
          <Cell
            key={colIdx}
            row={rowIdx}
            col={colIdx}
            color={color}
            backgroundColor={backgroundColor}
            label={label}
            shadow={shadow}
          />
        );
      }
    );
  }

  ChessBoard(displayBoard: DisplayStep[][]): ReactElement[] {
    return displayBoard.map(
      (row: DisplayStep[], rowIdx: number): ReactElement => (
        <Grid
          container
          key={rowIdx}
          className='board-row'
          direction='row'
          justify='space-between'>
          {this.Row(row, rowIdx)}
        </Grid>
      )
    );
  }

  render(): ReactElement {
    const {boardWrapper, boardImage, board, chessSteps} = this.props.classes;
    const {displayBoard}: Board = this.context.board;
    return (
      <Grid
        container
        direction='column'
        alignItems='center'
        spacing={3}
        className={boardWrapper}>
        <Grid className={board}>
          <Grid className={boardImage}>
            <img src={BoardImage} alt='board' />
          </Grid>
          <Grid
            className={chessSteps}
            container
            justify='center'
            alignItems='center'>
            {this.ChessBoard(displayBoard)}
          </Grid>
        </Grid>
      </Grid>
    );
  }
}
BoardComponent.contextType = Context;

export default withStyles(STYLE)(BoardComponent);
