/********************
 * Footer Component *
 ********************/
import React from 'react';

/** Icons. */
import {
  faGithub,
  faLinkedin,
  IconDefinition,
} from '@fortawesome/free-brands-svg-icons';
import {faIdBadge} from '@fortawesome/free-solid-svg-icons';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

/** Components. */
import {Button, Grid, Typography, withWidth} from '@material-ui/core';

/** Styles. */
import {withStyles} from '@material-ui/styles';
import STYLE from './Footer.style';

interface Link {
  name: string;
  link: string;
  icon: IconDefinition;
}

const LINKS: Link[] = [
  {name: 'Profile', link: 'https://www.zidianlyu.com', icon: faIdBadge},
  {
    name: 'LinkedIn',
    link: 'https://www.linkedin.com/in/zidianlyu/',
    icon: faLinkedin,
  },
  {name: 'Github', link: 'https://github.com/zidianlyu', icon: faGithub},
];

interface FooterProps {
  classes: {
    button: string;
    footer: string;
  };
  width: string;
}

class Footer extends React.Component<FooterProps> {
  render() {
    const {classes, width} = this.props;
    const isXs = width === 'xs';
    return (
      <Grid
        className={classes.footer}
        container
        direction='row'
        justify='space-between'
        spacing={1}>
        {LINKS.map(({name, link, icon}, idx) => (
          <Grid item xs={4} key={idx}>
            <Button
              className={classes.button}
              color='primary'
              fullWidth
              href={link}
              startIcon={<FontAwesomeIcon icon={icon} size='sm' />}
              variant='contained'>
              <Typography variant={isXs ? 'button' : 'h6'}>{name}</Typography>
            </Button>
          </Grid>
        ))}
      </Grid>
    );
  }
}

export default withWidth()(withStyles(STYLE)(Footer));
