/***********************
 * Evaluate Point Util *
 ***********************/
import {BOARD_SIZE} from '../../constants/game';
import {POINT_SCORE_MAP} from '../../constants/score';

/** Types. */
import {Score, Gap} from '../../types/score';
import {ScoreCache} from '../../types/cache';
import {MoveDirection} from '../../types/direction';
import {Role} from '../../types/role';

/**
 * Reinforce the score for FOUR related cases.
 * Returns the updated score.
 */
export const reinforceScore = (score: Score): Score => {
  if (score >= Score.BlockedFour && score < Score.Four) {
    if (score < Score.BlockedFour + Score.Three) {
      // does not make sense to BLOCKED_FOUR.
      return Score.Three;
    } else if (
      score >= Score.BlockedFour + Score.Three &&
      score < Score.BlockedFour * 2
    ) {
      // generates BLOCKED_FOUR and THREE, should be better than DOUBLE THREE.
      return Score.Four;
    } else {
      // DOUBLE BLOCKED_FOUR, should be better than FOUR.
      return Score.Four * 2;
    }
  }
  return score;
};

/** Calculate the gap for scoring. */
export const getGap = (emptyRoleIndex: number, sameRoleCount: number): Gap => {
  if (emptyRoleIndex <= Gap.Zero) {
    return Gap.Zero;
  }
  let gap: Gap = Gap.Default;
  const {One, Two, Three, Four, Five} = Gap;
  for (const gapCount of [One, Two, Three, Four, Five]) {
    if (
      emptyRoleIndex === gapCount ||
      emptyRoleIndex === sameRoleCount - gapCount
    ) {
      gap = gapCount;
      break;
    }
  }
  return gap;
};

/** Calculate the score for a given point. */
export const scorePoint = (
  emptyRoleIndex: number,
  blockRoleCount: number,
  sameRoleCount: number
): Score => {
  const gapCount: Gap = getGap(emptyRoleIndex, sameRoleCount);
  if (gapCount === Gap.Five || sameRoleCount - gapCount >= Gap.Five) {
    return Score.Five;
  }
  if (gapCount in POINT_SCORE_MAP) {
    const blockRoleMap = POINT_SCORE_MAP[gapCount];
    if (blockRoleCount in blockRoleMap) {
      const sameRoleMap = blockRoleMap[blockRoleCount];
      if (sameRoleCount in sameRoleMap) {
        return sameRoleMap[sameRoleCount];
      }
    }
  }
  return Score.Zero;
};

/**
 * Calculates the score after put the chess on the current position.
 * For performance purpose, if there is a input direction, we ONLY need to
 * calculate the score for 1 direction. Otherwise, we will calculate all 4
 * directions.
 * Returns the score of the point.
 */
export const evaluatePoint = (
  board: Role[][],
  scoreCache: ScoreCache,
  currentRow: number,
  currentCol: number,
  currentRole: Role,
  moveDirection?: MoveDirection
) => {
  const {Row, Col, Des, Asc} = MoveDirection;
  let totalScore = 0;
  let [emptyRoleIndex, blockRoleCount, sameRoleCount] = [-1, 0, 1];

  const withinBound = (row: number, col: number): boolean =>
    row >= 0 && col >= 0 && row < BOARD_SIZE && col < BOARD_SIZE;

  const resetCount = (): void => {
    [emptyRoleIndex, blockRoleCount, sameRoleCount] = [-1, 0, 1];
  };

  const hasSameRole = (nextRow: number, nextCol: number): boolean => {
    if (!withinBound(nextRow, nextCol)) {
      return false;
    }
    return board[nextRow][nextCol] === currentRole;
  };

  if (moveDirection === undefined || moveDirection === Row) {
    resetCount();
    for (
      let i = currentRow, j = currentCol + 1;
      i === currentRow && j < Infinity;
      i += 0, j++
    ) {
      if (!withinBound(i, j)) {
        blockRoleCount++;
        break;
      }
      const targetRole = board[i][j];
      if (targetRole === Role.Empty) {
        if (emptyRoleIndex === -1 && hasSameRole(i, j + 1)) {
          emptyRoleIndex = sameRoleCount;
          continue;
        } else {
          break;
        }
      } else if (targetRole === currentRole) {
        sameRoleCount++;
        continue;
      } else {
        blockRoleCount++;
        break;
      }
    }

    for (
      let i = currentRow, j = currentCol - 1;
      i === currentRow && j > -Infinity;
      i += 0, j--
    ) {
      if (!withinBound(i, j)) {
        blockRoleCount++;
        break;
      }
      const targetRole = board[currentRow][j];
      if (targetRole === Role.Empty) {
        if (emptyRoleIndex === -1 && hasSameRole(i, j - 1)) {
          emptyRoleIndex = 0;
          continue;
        } else {
          break;
        }
      } else if (targetRole === currentRole) {
        sameRoleCount++;
        emptyRoleIndex !== -1 && emptyRoleIndex++;
        continue;
      } else {
        blockRoleCount++;
        break;
      }
    }
    scoreCache[currentRole][Row][currentRow][currentCol] = scorePoint(
      emptyRoleIndex,
      blockRoleCount,
      sameRoleCount
    );
  }
  totalScore += scoreCache[currentRole][Row][currentRow][currentCol];

  // Case: |
  if (moveDirection === undefined || moveDirection === Col) {
    resetCount();
    for (
      let i = currentRow + 1, j = currentCol;
      i < Infinity && j === currentCol;
      i++, j += 0
    ) {
      if (!withinBound(i, j)) {
        blockRoleCount++;
        break;
      }
      const targetRole = board[i][currentCol];
      if (targetRole === Role.Empty) {
        if (emptyRoleIndex === -1 && hasSameRole(i + 1, j)) {
          emptyRoleIndex = sameRoleCount;
          continue;
        } else {
          break;
        }
      } else if (targetRole === currentRole) {
        sameRoleCount++;
        continue;
      } else {
        blockRoleCount++;
        break;
      }
    }

    for (
      let i = currentRow - 1, j = currentCol;
      i > -Infinity && j === currentCol;
      i--, j += 0
    ) {
      if (!withinBound(i, j)) {
        blockRoleCount++;
        break;
      }
      const targetRole = board[i][currentCol];
      if (targetRole === Role.Empty) {
        if (emptyRoleIndex === -1 && i > 0 && hasSameRole(i - 1, j)) {
          emptyRoleIndex = 0;
          continue;
        } else {
          break;
        }
      } else if (targetRole === currentRole) {
        sameRoleCount++;
        emptyRoleIndex !== -1 && emptyRoleIndex++;
        continue;
      } else {
        blockRoleCount++;
        break;
      }
    }
    scoreCache[currentRole][Col][currentRow][currentCol] = scorePoint(
      emptyRoleIndex,
      blockRoleCount,
      sameRoleCount
    );
  }
  totalScore += scoreCache[currentRole][Col][currentRow][currentCol];

  // Case: \
  if (moveDirection === undefined || moveDirection === Des) {
    resetCount();
    for (
      let i = currentRow + 1, j = currentCol + 1;
      i < Infinity && j < Infinity;
      i++, j++
    ) {
      if (!withinBound(i, j)) {
        blockRoleCount++;
        break;
      }
      const targetRole = board[i][j];
      if (targetRole === Role.Empty) {
        if (emptyRoleIndex === -1 && hasSameRole(i + 1, j + 1)) {
          emptyRoleIndex = sameRoleCount;
          continue;
        } else {
          break;
        }
      } else if (targetRole === currentRole) {
        sameRoleCount++;
        continue;
      } else {
        blockRoleCount++;
        break;
      }
    }

    for (
      let i = currentRow - 1, j = currentCol - 1;
      i > -Infinity && j > -Infinity;
      i--, j--
    ) {
      if (!withinBound(i, j)) {
        blockRoleCount++;
        break;
      }
      const targetRole = board[i][j];
      if (targetRole === Role.Empty) {
        if (emptyRoleIndex === -1 && hasSameRole(i - 1, j - 1)) {
          emptyRoleIndex = 0;
          continue;
        } else {
          break;
        }
      } else if (targetRole === currentRole) {
        sameRoleCount++;
        emptyRoleIndex !== -1 && emptyRoleIndex++;
        continue;
      } else {
        blockRoleCount++;
        break;
      }
    }
    scoreCache[currentRole][Des][currentRow][currentCol] = scorePoint(
      emptyRoleIndex,
      blockRoleCount,
      sameRoleCount
    );
  }
  totalScore += scoreCache[currentRole][Des][currentRow][currentCol];

  // Case: /
  if (moveDirection === undefined || moveDirection === Asc) {
    resetCount();
    for (
      let i = currentRow + 1, j = currentCol - 1;
      i < Infinity && j > -Infinity;
      i++, j--
    ) {
      if (!withinBound(i, j)) {
        blockRoleCount++;
        break;
      }
      const targetRole = board[i][j];
      if (targetRole === Role.Empty) {
        if (emptyRoleIndex === -1 && hasSameRole(i + 1, j - 1)) {
          emptyRoleIndex = sameRoleCount;
          continue;
        } else {
          break;
        }
      } else if (targetRole === currentRole) {
        sameRoleCount++;
        continue;
      } else {
        blockRoleCount++;
        break;
      }
    }

    for (
      let i = currentRow - 1, j = currentCol + 1;
      i > -Infinity && j < Infinity;
      i--, j++
    ) {
      if (!withinBound(i, j)) {
        blockRoleCount++;
        break;
      }
      const targetRole = board[i][j];
      if (targetRole === Role.Empty) {
        if (emptyRoleIndex === -1 && hasSameRole(i - 1, j + 1)) {
          emptyRoleIndex = 0;
          continue;
        } else {
          break;
        }
      } else if (targetRole === currentRole) {
        sameRoleCount++;
        emptyRoleIndex !== -1 && emptyRoleIndex++;
        continue;
      } else {
        blockRoleCount++;
        break;
      }
    }
    scoreCache[currentRole][Asc][currentRow][currentCol] = scorePoint(
      emptyRoleIndex,
      blockRoleCount,
      sameRoleCount
    );
  }
  totalScore += scoreCache[currentRole][Asc][currentRow][currentCol];
  return totalScore;
};
