/**********************
 * Provider Component *
 **********************/
import React, {Component, ReactElement} from 'react';

/** Models. */
import Board from '../../models/board';
import NegaMax from '../../models/negamax';

/** Types. */
import {Winner} from '../../types/board';
import {
  GameMode,
  GameState,
  GameStatus,
  AiLevel,
  GameMutationState,
} from '../../types/game';
import {Role} from '../../types/role';
import {SearchStep} from '../../types/search';

/** Utils. */
import {getWinner} from '../../utils/control';
import {buildGameState, buildStep} from '../../utils/generator';
import {nextRole} from '../../utils/next';

/** Component. */
import Context from './Context';

interface ProviderProps {
  children: ReactElement;
}

interface ProviderState extends GameState {
  dialogOpen: boolean;
  closeDialog: () => void;
  openDialog: () => void;
  placeChess: (row: number, col: number) => void;
  startNewGame: (mode: GameMode, firstHand: Role, aiLevel: AiLevel) => void;
}

export default class Provider extends Component<ProviderProps, ProviderState> {
  state = {
    ...buildGameState(),
    dialogOpen: false,
    closeDialog: (): void => this._closeDialog(),
    openDialog: (): void => this._openDialog(),
    placeChess: (row: number, col: number): void =>
      this._humanPlaceChess(row, col),
    startNewGame: (mode: GameMode, firstHand: Role, aiLevel: AiLevel): void =>
      this._initNewGame(mode, firstHand, aiLevel),
    stepBackward: (): void => this._stepBackward(),
    stepForward: (): void => this._stepForward(),
  };

  _initNewGame(mode: GameMode, firstHand: Role, aiLevel: AiLevel): void {
    this.setState(
      {
        ...buildGameState(),
        aiLevel,
        firstHand,
        mode,
        role: firstHand,
        status: GameStatus.Open,
      },
      () => {
        if (mode === GameMode.VsAgent && firstHand === Role.Agent) {
          this._agentPlaceChess();
        }
      }
    );
  }

  _closeDialog(): void {
    this.setState({dialogOpen: false});
  }

  _openDialog(): void {
    this.setState({dialogOpen: true});
  }

  _updateState(
    board: Board,
    role: Role,
    aiThinking: boolean = false,
    aiTimer: number = 0,
    aiScore: number = 0
  ): void {
    const winner: Winner = getWinner(board);
    let status: GameStatus = GameStatus.Open;
    if (winner.role !== Role.Empty) {
      status = GameStatus.Over;
      board.updateWinPoints(winner.winPoints);
      aiThinking = false;
    }
    const nextState: GameMutationState = {
      aiScore,
      aiTimer,
      aiThinking,
      board,
      role,
      status,
      winner,
    };
    this.setState({...nextState}, () => {
      if (
        this.state.mode === GameMode.VsAgent &&
        role === Role.Agent &&
        winner.role === Role.Empty
      ) {
        this._agentPlaceChess();
      }
    });
  }

  _agentPlaceChess(): void {
    setTimeout(() => {
      const {board, aiLevel} = this.state;
      const negamax = new NegaMax(board);
      const searchStep: SearchStep = negamax.getSearchStep(Role.Agent, aiLevel);
      const {timer, boardScore, point}: SearchStep = searchStep;
      const [aiRow, aiCol] = point;
      board.placeChess(buildStep(aiRow, aiCol), Role.Agent);
      this._updateState(board, Role.Human, false, timer, boardScore);
    }, 500);
  }

  _humanPlaceChess(row: number, col: number): void {
    const {board, role, mode} = this.state;
    if (mode === GameMode.VsAgent) {
      board.placeChess(buildStep(row, col), role);
      this._updateState(board, Role.Agent, true);
    }

    if (mode === GameMode.VsHuman) {
      board.placeChess(buildStep(row, col), role);
      this._updateState(board, nextRole(role));
    }
  }

  _stepBackward(): void {
    const {board, role, mode} = this.state;
    board.stepBackward(mode);
    this._updateState(board, role);
  }

  _stepForward(): void {
    const {board, role, mode} = this.state;
    board.stepForward(mode);
    this._updateState(board, role);
  }

  render(): ReactElement {
    return (
      <Context.Provider value={this.state}>
        {this.props.children}
      </Context.Provider>
    );
  }
}
