/** Constant. */
import {BOUND_LIMIT} from '../constants/ai';

/** Types. */
import {Step} from '../types/board';

/**
 * Checks if the AI need to consider the point.
 * Returns true if AI should consider the point.
 */
export const shouldConsiderStep = (
  heuristicStep: Step,
  steps: Step[]
): boolean => {
  if (!steps || !steps.length) {
    return false;
  }
  const [curRow, curCol] = heuristicStep.point;
  for (const step of steps) {
    const [endRow, endCol] = step.point;
    const [deltaX, deltaY] = [
      Math.abs(curRow - endRow),
      Math.abs(curCol - endCol),
    ];
    if (deltaX >= BOUND_LIMIT || deltaY >= BOUND_LIMIT) {
      return false;
    }
    // on star direction.
    if (!(curRow === endRow || curCol === endCol || deltaX === deltaY)) {
      return false;
    }
  }
  return true;
};
