/** Utils. */
import {buildTimer} from '../utils/generator';

/** Constant. */
import {TIME_LIMIT} from '../constants/ai';

export default class Timer {
  searchStartTime: number = buildTimer();
  timeout: number = TIME_LIMIT;

  reset(): void {
    this.searchStartTime = buildTimer();
  }

  get consumed(): number {
    return buildTimer() - this.searchStartTime;
  }

  get expired(): boolean {
    return this.consumed > this.timeout;
  }
}
