/*****************
 * App Component *
 *****************/
import React, {ReactElement} from 'react';

/** Components. */
import {CssBaseline, Divider, Fade, Grid, Paper} from '@material-ui/core';

/** Styles. */
import {withStyles} from '@material-ui/styles';

/** Components. */
import Board from '../board/Board';
import Footer from '../footer/Footer';
import Header from '../header/Header';
import Analyzer from '../analyzer/Analyzer';
import STYLE from './App.style';
import Context from '../provider/Context';
import {GameStatus, GameMode} from '../../types/game';

interface AppProps {
  classes: {
    app: string;
    paper: string;
  };
}

class App extends React.Component<AppProps> {
  render(): ReactElement {
    const {app, paper} = this.props.classes;
    return (
      <Grid container direction='row' justify='center' className={app}>
        <CssBaseline />
        <Fade in timeout={800}>
          <Paper elevation={5} square={false} className={paper}>
            <Header />
            <Divider />
            <Board />
            <Divider />
            {this.context.status === GameStatus.Open &&
              this.context.mode === GameMode.VsAgent && <Analyzer />}
            <Footer />
          </Paper>
        </Fade>
      </Grid>
    );
  }
}

App.contextType = Context;
export default withStyles(STYLE)(App);
