/******************
 * Generator Util *
 ******************/
/** Constant. */
import {BOARD_SIZE} from '../constants/game';

/** Model. */
import Board from '../models/board';

/** Types. */
import {DisplayStep, Step} from '../types/board';
import {ScoreCache} from '../types/cache';
import {MoveDirection} from '../types/direction';
import {GameMode, GameState, GameStatus, AiLevel} from '../types/game';
import {Role} from '../types/role';

/** returns an empty board. */
export const buildBoard = (): Role[][] => {
  const board: Role[][] = [];
  for (let i = 0; i < BOARD_SIZE; i++) {
    const row = [];
    for (let j = 0; j < BOARD_SIZE; j++) {
      row.push(Role.Empty);
    }
    board.push(row);
  }
  return board;
};

/** returns an empty display board. */
export const buildDisplayBoard = (): DisplayStep[][] => {
  const board: DisplayStep[][] = [];
  for (let i = 0; i < BOARD_SIZE; i++) {
    const row: DisplayStep[] = [];
    for (let j = 0; j < BOARD_SIZE; j++) {
      row.push({
        color: 'inherit',
        backgroundColor: 'inherit',
        shadow: 0,
        label: 0,
      });
    }
    board.push(row);
  }
  return board;
};

/** Constructs the default step. */
export const buildStep = (row: number, col: number): Step => ({
  point: [row, col],
  humanScore: 0,
  agentScore: 0,
  score: 0,
  role: Role.Empty,
});

/** returns an empty score cache. */
export const buildScoreCache = (): ScoreCache => ({
  [Role.Agent]: {
    [MoveDirection.Row]: buildBoard(),
    [MoveDirection.Col]: buildBoard(),
    [MoveDirection.Des]: buildBoard(),
    [MoveDirection.Asc]: buildBoard(),
  },
  [Role.Human]: {
    [MoveDirection.Row]: buildBoard(),
    [MoveDirection.Col]: buildBoard(),
    [MoveDirection.Des]: buildBoard(),
    [MoveDirection.Asc]: buildBoard(),
  },
});

/** Returns the default game state. */
export const buildGameState = (): GameState => ({
  aiLevel: AiLevel.easy,
  aiThinking: false,
  aiScore: 0,
  aiTimer: 0,
  board: new Board(),
  firstHand: Role.Human,
  mode: GameMode.VsAgent,
  role: Role.Human,
  status: GameStatus.Over,
  winner: {
    role: Role.Empty,
    winPoints: [],
  },
});

/**  returns date time in number. */
export const buildTimer = (): number => Number(new Date());
