/** Types. */
import {SearchRecord, SearchPoint} from '../types/search';

interface Cache {
  [hashCode: number]: SearchPoint;
}
export default class SearchCache {
  cache: Cache = {};

  reset(): void {
    this.cache = {};
  }

  set(hashCode: number, searchRecord: SearchRecord, searchDepth: number): void {
    const {boardScore, steps, stepCount, alphaBetaPrune} = searchRecord;
    if (alphaBetaPrune) {
      return;
    }
    const searchPoint: SearchPoint = {
      depth: searchDepth,
      record: {boardScore, steps, stepCount},
    };
    this.cache[hashCode] = searchPoint;
  }

  get(hashCode: number): SearchPoint | undefined {
    return this.cache[hashCode];
  }
}
