import React, {ReactElement} from 'react';

export const VerticalGap = ({size}: any): ReactElement => {
  let height = 12;
  switch (size) {
    case 'lg':
      height = 36;
      break;
    case 'md':
      height = 12;
      break;
    case 'sm':
      height = 8;
      break;
    case 'xs':
      height = 4;
      break;
    default:
      break;
  }
  return <div style={{height, width: 1}}></div>;
};

export const HorizontalGap = ({size}: any): ReactElement => {
  let width = 12;
  switch (size) {
    case 'lg':
      width = 36;
      break;
    case 'md':
      width = 12;
      break;
    case 'sm':
      width = 8;
      break;
    case 'xs':
      width = 4;
      break;
    default:
      break;
  }
  return <div style={{width, height: 1}}></div>;
};
