/** Model. */
import Board from '../models/board';

/** Types. */
import {Role} from './role';
import {Winner} from './board';

export enum GameMode {
  VsHuman,
  VsAgent,
}

export enum GameStatus {
  Open,
  Over,
}

export interface GameMutationState {
  aiTimer: number;
  aiScore: number;
  aiThinking: boolean;
  board: Board;
  role: Role;
  status: GameStatus;
  winner: Winner;
}

export interface GameState extends GameMutationState {
  aiLevel: AiLevel;
  firstHand: Role;
  mode: GameMode;
}

export enum AiLevel {
  easy = 4,
  medium = 6,
  hard = 8,
}
